 .customJumbotron:before {
     /*background: none, url('../img/werkspayce-bg.png') center center;*/
     -webkit-animation: bgColorMove 30s infinite;
     animation: bgColorMove 30s infinite;
     background-size: cover;
     content: "";
     position: fixed;
     left: 0;
     right: 0;
     z-index: -100;
     display: block;
     width: 100%;
     height: 100vh;
     background-size: cover;
     background-position: 100% 0%;
 }
 
 .customJumbotron {
     /*background: none, url('../img/werkspayce-bg.png') center center;*/
     -webkit-animation: bgColorMove 30s infinite;
